@font-face {
  font-family: dxcFont;
  src: url("/static/dxc-font/GT-Walsheim-Pro-Regular.woff2");
}
@font-face {
  font-family: dxcFontBold;
  src: url("/static/dxc-font/GT-Walsheim-Pro-Bold.woff2");
}

*,
.regular {
  font-family: dxcFont, Arial, Helvetica, sans-serif;
}

h1,
h2,
h3,
h4,
h5,
.bold {
  font-family: dxcFontBold, Arial, Helvetica, sans-serif;
}
